import React, { useContext, useEffect } from "react";
import "./datatable.scss";
import { DataGrid } from "@mui/x-data-grid";
import { userColumns, userRows } from "../../datatablesource";
import { Link, useHistory } from "react-router-dom";
import { useState } from "react";
import { styled, Box } from "@mui/system";
import ModalUnstyled from "@mui/base/ModalUnstyled";
import SelectBranch from "./SelectBranch";
import SelectRole from "./SelectRole";
import { useFormik, Form, FormikProvider } from "formik";
import * as Yup from "yup";
import axios from "axios";
import NoteContext from "../../RootContext/NoteContext";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";


const Datatable = () => {
  const { currentUser, setCurrentUser, authToken, setAuthToken } =
    useContext(NoteContext);
  const history = useHistory();

  const [data, setData] = useState(userRows);

  const handleDelete = (id) => {
    setData(data.filter((item) => item.id !== id));
  };

  const actionColumn = [
    {
      field: "action",
      headerName: "Action",
      width: 200,
      renderCell: (params) => {
        return (
          <div className="cellAction">
            {/* <Link to="/users/test" style={{ textDecoration: "none" }}>
              <div className="viewButton">View</div>
            </Link> */}
            <div
              className="deleteButton"
              onClick={() => handleDelete(params.row.id)}
            >
              Delete
            </div>
          </div>
        );
      },
    },
  ];


  const LoginSchema = Yup.object().shape({
    email: Yup.string()
      .email("Email must be a valid email address")
      .required("Email is required"),
    SelectRole: Yup.string().required("Role is required"),
    name: Yup.string().required("Name is required"),
    SelectBranch: Yup.string().required("Branch is required"),
    password: Yup.string().required("Password is required"),
  });

  const StyledModal = styled(ModalUnstyled)`
  position: fixed;
  z-index: 1300;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;

  const Backdrop = styled("div")`
  z-index: -1;
  position: fixed;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-tap-highlight-color: transparent;
`;

  const style = {
    width: 600,
    bgcolor: "background.paper",
    p: 2,
    px: 4,
    pb: 3,
  };
  const options = [
    { value: "Admin", label: "Admin" },
    { value: "Manager", label: "Manager" },
    { value: "Officer", label: "Officer" },
  ];

  const optionsBranch = [
    { value: "Sydney", label: "Sydney" },
    { value: "Islamabad", label: "Islamabad" },
    { value: "Melbourne", label: "Melbourne" },
    { value: "Tasmania", label: "Tasmania" },
    { value: "Lahore", label: "Lahore" },
    { value: "India", label: "India" },
    { value: "Multan", label: "Multan" },
    { value: "Srilanka", label: "Srilanka" },
    { value: "Saudi Arabia", label: "Saudi Arabia" },
    { value: "Faiselabad", label: "Faiselabad" },
  ];




  const formik = useFormik({
    initialValues: {
      name: "",
      email: "",
      password: "",
      SelectRole: "",
      SelectBranch: "",
      image: "data:image/png;base64,"

    },
    validationSchema: LoginSchema,


    onSubmit: (values) => {
      alert("check");
      return;
      axios.post("http://localhost:3001/api/auth/register", {

        name: values.name,
        email: values.email,
        password: values.password,
        role: values.SelectRole,
        branch: values.SelectBranch,
        image: "data:image/png;base64,"


      }, {
        headers: {
          authorization: authToken
        }
      }).then((response) => {
        if (response.status === 400 || !response) {
          alert("please fill correct data ")
        } else {
          alert("Successfully Registration")
          history.push("/");
        }

      })
      //   history.push("/dashboard");
    },
  });

  const { errors, touched, values, isSubmitting, handleSubmit, getFieldProps } =
    formik;

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [userData, setuserData] = useState([]);

  useEffect(() => {
    axios.get('http://localhost:3001/api/auth/users', {
      headers: {
        authorization: authToken
      }
    })
      .then((response) => {
        console.log(response);
        setuserData(response.data)
      })
  }, [])



  return (
    <div className="datatable">
      <div className="datatableTitle">
        Add New User
        <button className="btn btn-success" onClick={handleOpen}> Add New</button>


      </div>
      <TableContainer component={Paper} className="table">

        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>

              <TableCell className="tableCell">User</TableCell>
              <TableCell className="tableCell">Name</TableCell>
              <TableCell className="tableCell">Email</TableCell>
              <TableCell className="tableCell">Role</TableCell>
              <TableCell className="tableCell">Branch</TableCell>

              <TableCell className="tableCell">Date</TableCell>

            </TableRow>
          </TableHead>
          <TableBody>
            {userData && userData.map((list, id) => (
              <TableRow key={list.id}>
                <TableCell className="tableCell">
                  <div className="cellWrapper">
                    <img src={list.image} alt="" className="image" />
                  </div>
                </TableCell>
                <TableCell className="tableCell">{list.name}</TableCell>
                <TableCell className="tableCell">{list.email}</TableCell>

                <TableCell className="tableCell">{list.role}</TableCell>
                <TableCell className="tableCell">{list.branch}</TableCell>

                <TableCell className="tableCell">{list.date}</TableCell>

              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      {/* <DataGrid
        className="datagrid"
        rows={data}
        columns={userColumns.concat(actionColumn)}
        pageSize={9}
        rowsPerPageOptions={[9]}
        checkboxSelection
      /> */}


      <StyledModal
        aria-labelledby="unstyled-modal-title"
        aria-describedby="unstyled-modal-description"
        open={open}
        onClose={handleClose}
        BackdropComponent={Backdrop}
      >
        <Box
          sx={style}
          style={{ backgroundColor: "#ffffff", borderRadius: "10px" }}
        >
          <form autoComplete="off" onSubmit={handleSubmit}>
            <div className="row pt-3">
              <div className="col-md-12">
                <div class="row g-3">
                  <div class="col">
                    <input
                      type="text"
                      class="form-control"
                      placeholder="Full Name"
                      aria-label="First name"
                      name="name"
                      value={formik.values.name}
                      onChange={formik.handleChange}
                    />
                    {formik.errors.name && <p className="handleerror">{formik.errors.name}</p>}

                  </div>
                  <div class="col">
                    <input
                      type="email"
                      class="form-control"
                      placeholder="Email"
                      aria-label="First name"
                      name="email"
                      value={formik.values.email}
                      onChange={formik.handleChange}
                    />
                    {formik.errors.email && <p className="handleerror">{formik.errors.email}</p>}
                  </div>
                </div>
                <div className="row pt-3">
                  <div className="col-md-12">
                    <div class="row g-3">
                      <div class="col">
                        <SelectRole
                          className="input"
                          onChange={(value) =>
                            formik.setFieldValue("SelectRole", value.value)
                          }
                          value={formik.values.SelectRole}
                          options={options}
                        />
                        {formik.errors.SelectRole && <p className="handleerror">{formik.errors.SelectRole}</p>}

                      </div>
                      <div class="col">
                        <SelectBranch
                          className="input"
                          onChange={(value) =>
                            formik.setFieldValue(
                              "SelectBranch",
                              value.value
                            )
                          }
                          value={formik.values.SelectBranch}
                          options={optionsBranch}
                        />
                        {formik.errors.SelectBranch && <p className="handleerror">{formik.errors.SelectBranch}</p>}

                      </div>
                    </div>
                  </div>
                </div>
                <div className="row pt-3">
                  <div className="col-md-12">
                    <div class="row g-3">


                      <div class="col">
                        <input
                          type="password"
                          class="form-control"
                          placeholder="password"
                          aria-label="First name"
                          name="password"
                          value={formik.values.password}
                          onChange={formik.handleChange}
                        />
                        {formik.errors.password && <p className="handleerror">{formik.errors.password}</p>}

                      </div>
                    </div>
                  </div>
                </div>

                {/* {formik.errors.SelectRole ? (
            <div className="error">{formik.errors.SelectRole}</div>
          ) : null} */}
                <button type="submit" className="btn btn-primary mt-5">
                  Create User
                </button>
              </div>
            </div>
          </form>
        </Box>
      </StyledModal>


    </div>
  );
};

export default Datatable;
